.vision {
    background: center / cover no-repeat url("/img/vision-bg.jpg") #000;
    padding: 50px 0;

    &_container {
        min-height: 300px;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        flex-wrap: wrap;
        position: relative;

        &:after {
            content: url("/img/dove.png");
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%, -50%, 0);
            z-index: 0;
        }
    }

    &_title-img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%, -50%, 0);
    }

    &_title {
        @extend .big-title;
        color: #fff;
        width: 100%;
        max-width: 550px;
        position: relative;
        transform: translateY(calc(50% + 20px));
        z-index: 1;

        &:after {
            content: '';
            width: 100%;
            height: 25px;
            position: absolute;
            bottom: 2px;
            left: 0;
            background: rgba($persian-green, .5);
            z-index: -1;
        }
    }

    &_link-wrapper {
        width: 100%;
    }

    &_link {
        cursor: pointer;
        position: relative;
        display: flex;
        justify-content: center;
        text-decoration: none;
        align-items: center;
        margin: 0 auto 15px;
        padding-bottom: 2px;
        background-color: #fff;
        color: #000;
        font-size: 13px;
        font-weight: bold;
        width: 130px;
        height: 30px;
        border-radius: 15px;
        transition: all .2s ease-out;
        z-index: 1;

        &:hover {
            filter: drop-shadow(0 0 7px #fff);
        }
    }
}